<template>
    <nav aria-label="Page navigation example">
        <ul class="pagination" v-if="pages > 1">

            <router-link
                    tag="a"
                    class="page-item previous"
                    :style="{'visibility': current === 1 ? 'hidden' : 'visible'}"
                    :to="'/' + classification + '/page/' + (current-1)"
                    @click.native="getSpecificPage(current-1)">
<!--                <a class="page-link" aria-label="Previous">-->
                    <span aria-hidden="true">&laquo; Prev</span>
<!--                </a>-->
            </router-link>

            <router-link
                    tag="a"
                    class="page-item next"
                    :style="{'visibility': current === pages ? 'hidden' : 'visible'}"
                    :to="'/' + classification + '/page/' + (current+1)"
                    @click.native="getSpecificPage(current+1)">
<!--                <a class="page-link" aria-label="Next">-->
                    <span aria-hidden="true">Next &raquo;</span>
<!--                </a>-->
            </router-link>

        </ul>

    </nav>

</template>

<script>

    export default {
        name: "Pagination",
        props: {
            pages: {
                type: Number,
                default: 8,
            },
            classification: {
                type: String,
                default: '',
            }
        },
        data() {
            return {
                loading: true,
                loaded: false,
                errored: false,
                current: 1, //当前页数
            }
        },
        methods: {
            getSpecificPage: function (index) {
                if(index >= 1 && index <= this.pages) {
                    this.axios
                        .get(/*process.env.VUE_APP_HOST + '/blog/page/' + index*/)
                        .then(response => {
                            this.blogs = response.data.body
                            this.loaded = true
                            this.current = index    //修改当前页
                            console.log(this.blogs)
                        })
                        .catch(error => {
                            console.log(error)
                            this.errored = true
                            //TODO:错误发生要给出显式化的反馈
                        })
                        .finally(() => this.loading = false)
                    this.current = index    //测试用的
                }
            }
        },
    }
</script>

<style scoped>
    nav {
        margin: 2% 0;
        width: 100%;
    }
    .pagination {
        justify-content: space-between;
    }
    .previous {
        justify-content: left;
    }
    .next {
        justify-content: right;
    }
    a {
        color: #999999;
        border: none;
        border-radius: 5px;
    }
    a:hover {
        color: #abb2b9 ;
    }

</style>
